.animated-logo {
  position: absolute;
  top: 0;
  left: 0;
}

.App-logo1 {
  position: absolute;
  top: -50px;
  left: -50px;
  height: 100px;
  width: 100px;
  pointer-events: none;
  z-index: 1;
}

.App-logo2 {
  position: absolute;
  top: -35px;
  left: -29px;
  height: 100px;
  width: 100px;
  pointer-events: none;
  z-index: 1;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo1-login {
    -webkit-animation: App-logo-spin 3s ease-out;
            animation: App-logo-spin 3s ease-out;
  }
  .App-logo2-login {
    -webkit-animation: App-logo-spin-rev 3s ease-out;
            animation: App-logo-spin-rev 3s ease-out;
  }
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo1-register {
    -webkit-animation: App-logo-up-1 1s ease-out;
            animation: App-logo-up-1 1s ease-out;
  }
  .App-logo2-register {
    -webkit-animation: App-logo-up-2 1s ease-out;
            animation: App-logo-up-2 1s ease-out;
  }
}
@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(180deg);
    top: -30vh;
    left: -30vw;
    opacity: 0.1;
  }
  to {
    transform: rotate(0deg);
    top: -50px;
    left: -50px;
    opacity: 1;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(180deg);
    top: -30vh;
    left: -30vw;
    opacity: 0.1;
  }
  to {
    transform: rotate(0deg);
    top: -50px;
    left: -50px;
    opacity: 1;
  }
}
@-webkit-keyframes App-logo-spin-rev {
  from {
    transform: rotate(360deg);
    top: -30vh;
    left: 40vw;
    opacity: 0.1;
  }
  to {
    transform: rotate(0deg);
    top: -35px;
    left: -29px;
    opacity: 1;
  }
}
@keyframes App-logo-spin-rev {
  from {
    transform: rotate(360deg);
    top: -30vh;
    left: 40vw;
    opacity: 0.1;
  }
  to {
    transform: rotate(0deg);
    top: -35px;
    left: -29px;
    opacity: 1;
  }
}
@-webkit-keyframes App-logo-up-1 {
  from {
    top: -50px;
  }
  to {
    top: -50px;
  }
}
@keyframes App-logo-up-1 {
  from {
    top: -50px;
  }
  to {
    top: -50px;
  }
}
@-webkit-keyframes App-logo-up-2 {
  from {
